

.form__form  {
    margin-right: 1.6em;
      padding: 1.5em 2em;
      border-radius: 1em;
      background-color: $light;
      width: 28.3em;
      height: 30em;
}

.header {
    padding-bottom: 1em;
    display: flex;
    align-items: center;
    height: 4em;
    border-bottom: 0.05em solid $border;

    &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: .4em;
        font-weight: 100!important;
        color: #ccc;
        border: 0.05em solid #e5e5e5;
        border-radius: 50%;

        &__icon {
            font-size: 1.7em;
            color: rgb(73, 73, 73);
        }
    }

    &__title {
        margin-left: .5em;
        font-weight: 500;
        font-size: 1.4em;
    }

}



.form {
    padding: 2em 0 1em 0;
    display: flex;
    flex-direction: column;

    &__label {
        margin-bottom: 1.2em;
        padding-left: .8em;
        position: relative;
        display: inline-block;
        font-size: 1.2em;
        color: rgb(59, 59, 59);

        &::after {
            content:"";
            position: absolute;
            top: .4em;
            left: 0;
            width: .3em;
            height: .4em;
            background-color: #1a1a1a;
            border-radius: 50%;
        }
    }

    &__blog {
        margin-bottom: .5em;
        position: relative;
    }

    &__span {
        position: absolute;
        top: .53em;
        left: .7em;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2em;
        height: 2em;
        border-radius: .4em;
        border: 0.05em solid $border;
        border: 0.05em solid rgb(12, 154, 214);
        background-color: white;
        z-index: 2;

        &__icon {
            font-size: 1.2em;
            color: rgb(12, 154, 214);
        }
    }

    &__input {
        position: relative;
        padding: 1.21em 1em;
        padding-left: 4.2em;
        width: 75%;
        border: 0.05em solid $border;
        outline: 0;
        border-radius: .5em;
        transition: border-color .3s linear;
        z-index: 2;

        &:focus {
            border-color: rgb(12, 154, 214);
            &::placeholder {
                color: rgb(12, 154, 214);
                transform: translateY(97%);
            }
        }

        &::placeholder {
            color: #b8b8b8;
            text-transform: capitalize;
            transition: color .3s linear, transform 0.3s linear;
        }
    }

    &__response__error {
        margin-bottom: 1em;
        // display: none;
        color: red;
        font-weight: 300;
        border-radius: .5em;
    }
    
    &__button {
        margin-top: 1em;
        margin-bottom: 2em;
        position: relative;
        padding: 1.1em 0;
        font-weight: 700;
        width: 10em;
        border: 0;
        // border: 0.05em solid rgba(21, 119, 161, 0.147);
        background: rgb(12, 154, 214);
        border-radius: .5em;
        outline: 0;
        color: $light;
        overflow: hidden;
        cursor: pointer;
        z-index: 1;

        &::after {
            content:"";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgb(43, 43, 43);
            z-index: -1;
            clip-path: polygon(14% 45%, 43% 41%, 48% 70%, 79% 63%, 100% 100%, 53% 100%, 0 100%, 0% 70%, 0 0, 31% 17%);
            transition: clip-path .5s ease-in-out;
        }

        &:hover {
            &::after {
                clip-path: polygon(45% 0, 96% 0, 0 100%, 100% 0, 100% 100%, 0 100%, 0 3%, 100% 99%, 0 0, 19% 0);
            }
        }
    }

    &__text {
        text-decoration: underline;
        color: rgb(12, 154, 214);
        font-weight: 200;
        font-size: .9em;
    }
}