


.contacts {
    padding: 1em 1em;
    background: $light;
    width: 30em;
    height: 40em;
    border-radius: 1em;

    &__header {
        padding: 0 1em;
        display: flex;
        align-items: center;
        // background: green;
        border-bottom: .05em solid $border;
        height: 4.5em;

        &__btn {
            position: relative;
            margin-right: 5.5em;
            padding: 1.3em .5em ;
            outline: 0;
            border: 0;
            background: rgb(12, 154, 214);
            border-radius: .7em;
            width: 11em;
            text-transform: capitalize;
            font-kerning: auto;
            font-weight: 900;
            color: $light;
            cursor: pointer;
            overflow: hidden;
            z-index: 1;

            &::after {
                content:"";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: rgb(43, 43, 43);
                z-index: -1;
                clip-path: polygon(25% 26%, 24% 44%, 54% 45%, 59% 72%, 88% 79%, 100% 100%, 0 100%, 0 0, 11% 15%);
                transition: clip-path .3s ease-in-out;
            }
    
            &:hover {
                &::after {
                    clip-path: polygon(34% 0, 56% 0, 74% 0, 100% 0, 100% 88%, 100% 100%, 0 100%, 0 0, 11% 0);
                }
            }
        }

        &__icon {
            position: relative;
            border-radius: 50%;
            color: #ccc;
            border-radius: 50%;
        }

        .iconsi {
            font-size: 1.45em!important;
            color: rgb(12, 154, 214)!important;
        }

        .contact__header__counter {
            position: absolute;
            top: -.9em;
            right: -.4em;
            font-size: 1.3em;
            color: red;
            font-weight:6700;
        }
    }




    &__list {
        margin-top: 1.7em;
        padding: .5em 1em;
        display: flex;
        flex-direction: column;
        // border: 1px solid red;
        overflow-y: scroll;
        height: 30em;

        &::-webkit-scrollbar {
            // background: rgb(232, 232, 232);
            width: 0.7em;
        }

        &::-webkit-scrollbar-thumb {
            background: rgb(12, 154, 214);
        }
    }

    &__card  {
        margin-bottom: 1.5em;
        padding: 1em;
        display: flex;
        width: 100%;
        min-height: auto;;
        border: 1px solid $border;
        border-radius: .5em;
        background: lighten($border, 11.7 );

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
    }

    &__buttons {
        display: flex;
        justify-content: flex-end;
        width: 16em;
        // border: 1px solid red;
    }

    &__button {
        padding: .7em 1em;
        margin-right: 1em;
        cursor: pointer;
        border: 0;
        // border: .05em solid $border;
        border-radius: .5em;
        background: transparent;
        transition: background 0.4s ease-in-out, color 0.3s linear;

        &:last-child {
            margin-right: 0;
        }
    }

    &__importance {
        background: rgba(255, 166, 0, 0.153);
        color: $light;
        border: .05em solid rgba(255, 166, 0, 0.408);
        color: rgb(255, 166, 0);
        // background: rgba(0, 0, 0, 0.065);
        // color: $light;
        // border: .05em solid rgba(0, 0, 0, 0.218);
        // color: black;

        &:hover {
            background:  rgb(255, 166, 0);
            color: $light;
        }
    }


    &__delete {
        background: rgba(250, 44, 44, 0.178);
        color: $light;
        border: .05em solid rgba(255, 0, 0, 0.297);
        color: red;

        &:hover {
            background: red;
            color: $light;
        }
    }

    &__name {
        margin-bottom: .5em;
        font-size: 1.5em;
        font-weight: 500;
    }

    &__num {
        margin-bottom: 1em;
        text-decoration: underline;
        font-size: 1em;
        display: flex;
        font-weight: 400;
        color: rgb(12, 154, 214);
    }

    &__phone {
        margin-right: .4em;
        font-size: 1.2em;
        color: rgb(12, 154, 214);
    }

    &__image {
        margin-right: 1em;
        border: 1px solid $border;
        min-width: 4em;
        height: 4em;
        border-radius: 50%;;
    }

    &__img {
        object-fit: cover;
        object-position: center center;
        width: 100%;
        height: 100%;
    }

}

.no {
    padding-top: 2em;
    display: flex;
    // align-items: center;
    justify-content: center;
    height: 100%;

    &__card {
        text-align: center;
    }

    &__img {
        margin-bottom: 2em;
        width: 15.5em;
        height: 15.5em;
        object-fit: contain;
        object-position: center center;
    }
    
    &__question {
        margin-bottom: 1em;
        font-size: 1.5em;
        color: rgb(12, 154, 214);
    }
}