

@import url('https://fonts.googleapis.com/css2? family= Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200; 1 300 ;1 400 ;1 500 ;1 600 ;1 700 ;1 800 ;1 900 & display=swap');


*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

$light : white;
$border: #dddddd;

body {
    font-family: 'Raleway', sans-serif;
    width: 100%;
    height: 100%;
    background-color: #ececec;
}

.main {
    margin: 5em auto;
    max-width: 70em;
    // border: 1px solid red;
}

.container {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
}

// @///
@import './form';
@import './contact';



// Error and Succes 

.error {
    position: absolute;
    bottom: 3em;
    display: flex;
    align-items: center;
    padding: 1em 1em;
    border-radius: .7em;
    background: rgba(250, 44, 44, 0.178);
    color: $light;
    border: .05em solid rgba(255, 0, 0, 0.297);
    color: red;

    &__icon {
        border: .05em solid red;
    }
}


.success {
    position: absolute;
    bottom: 3em;
    display: flex;
    align-items: center;
    padding: 1em 1em;
    border-radius: .7em;
    background: rgba(0, 198, 0, 0.235);
    color: $light;
    border: .05em solid rgba(0, 177, 0, 0.872);
    color: green;

    &__icon {
        border: .05em solid green;
        color: green!important;
    }
}


.response__icon {
    margin-right: 1em;
    padding: .1em;
    font-size: 1.1em;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
}